import React, { Component } from 'react';
import "./index.css";
import { Dialog, DialogTitle, DialogContent, DialogContentText, Button } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import { toastr } from 'react-redux-toastr';
import socketIO from 'socket.io-client';
import { BASE_URL } from '../../../../constants/index';
import { withdrawPortfolio } from '../../../../reducers/studentReducer';
import moment from 'moment';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

class StudentHome extends Component {

    constructor() {
        super();
        this.state = {
            modal: false,
            withdraw: false,
            confirmed: false,
            currentOrderNumber: 0,
            averageTimePerStudent: "",
        }

        this.openModal = this.openModal.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }

    componentDidMount() {
        if (!this.props.student.active) {
            toastr.info("Nu uita s i actualizezi parola din fereastra Profil");
        }

        if (!this.props.student.enrolled && this.props.student.orderNumber !== 0) {
            this.mountSocket();
        }

        //extract to a method

    }

    mountSocket() {
        const facultyId = this.getFacultyId();

        const socket = socketIO(BASE_URL, { query: 'facultyId=' + facultyId });

        socket.on(facultyId, (message) => {
            this.setState({
                currentOrderNumber: message.currentOrderNumber,
                averageTimePerStudent: message.averageTimePerStudent,
            });
        })
    }

    getFacultyId() {
        return this.props.student.options ? this.props.student.options[0].faculty_profile.facultyId
            : this.props.student.temporaryFacultyId
    }

    calculateEntryTime() {
        const [minutes, seconds] = this.state.averageTimePerStudent.split(":");

        if (this.props.student.orderNumber > this.state.currentOrderNumber) {
            let travel = moment().format("HH:MM");

            travel = moment().add(minutes * (this.props.student.orderNumber - this.state.currentOrderNumber), "minutes").format("HH:MM");
            travel = moment().add(seconds * (this.props.student.orderNumber - this.state.currentOrderNumber), "seconds").format("HH:MM");

            return travel;
        }
    }

    openModal() {
        this.setState({ modal: true })
    }

    handleClose() {
        this.setState({ modal: false })
    }

    isAdmitted() {
        const option = this.props.student.options.find(option => option.admitted);
        return option ? (option.faculty_profile.name + (option.faculty_profile.type === "B" ? " - BUGET" : " - TAXA")) : "";
    }

    render() {
        return (
            <div>
                <Dialog open={this.state.modal} onClose={this.handleClose} TransitionComponent={Transition}
                    keepMounted aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description">
                    <DialogTitle id="alert-dialog-slide-title">Informaii utile</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-slide-description">
                            Acceseaz link-urile urmtoare pentru a afla mai multe despre procesul de admitere poi accesa:
                        </DialogContentText>
                        <DialogContentText id="alert-dialog-slide-description">
                            <a href="https://ase.ro">Academia de Studii Economice din Bucureti</a>
                        </DialogContentText>
                        <DialogContentText id="alert-dialog-slide-description">
                            <a href="http://sisc.ro">Sindicatul Studenilor din cibernetic</a>
                        </DialogContentText>
                    </DialogContent>
                </Dialog>
                <h2>Bun, {this.props.student.firstname}</h2>
                <div className="student-info">
                    {this.isAdmitted() ? <h3>Ai fost repartizat la {this.isAdmitted()}</h3>
                        : (this.props.orderNumber !== 0 ?
                            <h3>Starea curent: Nerepartizat</h3>
                            : "")
                    }
                    {
                        ((!this.props.student.enrolled) && (this.props.student.orderNumber !== 0)) ?
                            <div className="student-home-cards-container">
                                <Card className="student-home-card">
                                    <CardContent>
                                        <Typography variant="h2" component="h2">
                                            {this.props.student.orderNumber}
                                        </Typography>
                                        <Typography variant="h6" component="h6">
                                            bonul tu de ordine
                                        </Typography>
                                    </CardContent>
                                </Card>
                                <Card className="student-home-card">
                                    <CardContent>
                                        <Typography variant="h2" component="h2">
                                            {this.state.currentOrderNumber}
                                        </Typography>
                                        <Typography variant="h6" component="h6">
                                            bonul de ordine curent
                                        </Typography>
                                    </CardContent>
                                </Card>
                                <Card className="student-home-card">
                                    <CardContent>
                                        <Typography variant="h2" component="h2">
                                            {this.state.averageTimePerStudent}
                                        </Typography>
                                        <Typography variant="h6" component="h6">
                                            timpul mediu de ateptare/student
                                        </Typography>
                                    </CardContent>
                                </Card>
                                {this.props.student.orderNumber > this.state.currentOrderNumber ?
                                    <Card className="student-home-card">
                                        <CardContent>
                                            <Typography variant="h2" component="h2">
                                                {this.calculateEntryTime()}
                                            </Typography>
                                            <Typography variant="h6" component="h6">
                                                ora estimat de intrare
                                        </Typography>
                                        </CardContent>
                                    </Card> : ""
                                }
                            </div>
                            : <div>
                                {!this.isAdmitted() && this.props.student.orderNumber !== 0 ?
                                    <div>
                                        <h3>Felicitri! Ai finalizat cu succes procesul de nscriere</h3>
                                        <h4>Orice modificare a strii curente va fi semnalat att aici,
                                            ct i prin trimiterea unui email</h4>
                                    </div>
                                    : <div>
                                        <h3>Felicitri! Eti pe punctul de a face primul pas spre cea mai frumoas perioad a vieii!</h3>
                                        <p>Dac nu eti sigur cu privire la datele introduse, le poi actualiza n pagina <strong>Profil</strong></p>
                                        <p>Pentru a eficientiza procesul de nscriere, i poi selecta opiunile n pagina <strong>Opiuni</strong></p>
                                        <p>Pentru a selecta opiuni, trebuie, mai nti s achiziionezi credite</p>
                                        <p>Te vezi deja stnd la coad? Din fericire, poi achiziiona bonuri din aceeai pagin, fr nicio grij</p>
                                        <p>n ziua nscrierii, solicit un bon de ordine din pagina <strong>nscriere</strong></p>
                                        <p>Pentru ca bonul s fie generat, trebuie s te afli n proximitatea facultii pentru care optezi</p>
                                    </div>
                                }
                                {
                                    this.props.student.orderNumber === 0 ?
                                        "" :
                                        (!this.props.student.withdrawPortfolio ?
                                            <div>
                                                <h5>Dac vrei s i retragi dosarul, poi apsa pe butonul de mai jos pentru a fi
                                                repartizat la o coad special n ziua retragerii</h5>
                                                <Button type="submit" color="primary" variant="contained" label="Submit"
                                                    onClick={() => this.props.withdrawPortfolio()}>Retragere Dosar</Button>
                                            </div>
                                            :
                                            <div>
                                                <h5>
                                                    Prezint-te la coada special pentru retragerea dosarului</h5>
                                                <h5>Dac te-ai rzgndit, nu este nicio problem!</h5>
                                                <h5>Nu vei fi exclus din repartizare dect din momentul n care i retragi fizic dosarul</h5>
                                            </div>)
                                }
                            </div>
                    }

                    <h4 onClick={this.openModal} className="student-profile-modal">
                        Poi gsi mai multe informaii despre nscriere aici
                    </h4>
                    <p><a href="https://ase.ro" target="_blank" rel="noopener noreferrer">Ai o nelmurire? Contacteaz-ne!</a></p>
                </div>
            </div>
        )
    }
}

const mapStateToProps = ({ studentReducer }) => ({
    role: studentReducer.role,
    student: studentReducer,
});

const mapDispatchToProps = { withdrawPortfolio };

export default connect(mapStateToProps, mapDispatchToProps)(StudentHome);